.profile__edit-button {
  max-width: 24px;
  align-self: center;
  padding: 0;
  border: none;
  background: none;
  margin: 7px 0 0;
}
@media screen and (min-width: 320px) and (max-width: 1023px) {
  .profile__edit-button {
    max-width: 18px;
    margin-top: 16px;
    margin-left: 1px;
  }
}
.profile__edit-button:hover {
  opacity: 0.6;
  cursor: pointer;
}
