.profile__avatar-container {
  height: 120px;
  width: 120px;
  border-radius: 50%;
  display: flex;
  align-items: center;
}

.profile__avatar-container:hover {
  opacity: 0.5;
}
@media screen and (min-width: 320px) and (max-width: 1023px) {
  .profile__avatar-container {
    margin: 0 auto;
  }
}
