.profile__add-button {
  box-sizing: border-box;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  margin: auto;
  padding: 0;
  width: 100%;
  max-width: 150px;
  background: url(../../../images/addButton.svg) no-repeat center;
  border: 2px solid #fff;
  height: 50px;
  border-radius: 2px;
}

@media screen and (min-width: 320px) and (max-width: 1023px) {
  .profile__add-button {
    max-width: 282px;
    margin-top: 36px;
    background: url(../../../images/addButtonLong.svg) no-repeat center;
  }
}

.profile__add-button:hover {
  opacity: 0.6;
  cursor: pointer;
}
