.popup-img__close-button {
  position: absolute;
  top: -32px;
  right: -32px;
  background: none;
  border: none;
  display: flex;
  padding: 0;
}
.popup-img__close-button:hover {
  opacity: 0.6;
  cursor: pointer;
}
@media screen and (min-width: 320px) and (max-width: 1023px) {
  .popup-img__close-button {
    top: -25px;
    right: -25px;
  }
}
