.popup__input {
  width: 350px;
  text-decoration: none;
  border: none;
  outline: none;
  display: flex;
  border-bottom: 1px rgba(0, 0, 0, 0.2) solid;
  margin-top: 30px;
  padding-bottom: 13px;
}

@media screen and (min-width: 320px) and (max-width: 1023px) {
  .popup__input {
    width: 200px;
    margin-top: 20px;
  }
}
