.profile__avatar-edit-button {
  padding: 0;
  opacity: 0;
  width: 120px;
  height: 120px;
  border: none;
  background-image: url("../../../images/pen.svg");
  background-repeat: no-repeat;
  cursor: pointer;
  transition: opacity 0.4s;
  background-color: transparent;
  background-position: 50%;
  border-radius: 50%;
  position: absolute;
}

.profile__avatar-edit-button:hover {
  opacity: 1;
}
